import React from "react";
import { Dropdown, Button, Input, Modal } from "semantic-ui-react";
import { connect } from "react-redux";
import { addPaymentToBill } from "../../actions/CashRegisterActions";
import { getTodayJalaali } from "../utils/jalaaliUtils";
import { toastr } from "react-redux-toastr";

class AddPaymentModal extends React.Component {
  state = {
    paymentOptions: [
      {
        key: "cheque",
        text: "",
        value: "cheque"
      },
      {
        key: "card",
        text: "  ",
        value: "cash_card"
      }
    ],
    card_amount: this.props.price,
    cash_amount: 0
  };

  setStateType = (_, { value }) => {
    this.setState({ type: value });
  };

  handleInputChange = (e, status) => {
    this.setState({
      [status]: e.target.value
    });
  };

  handleSubmit = () => {
    let card_amount =
      Number(this.state.card_amount) + Number(this.state.cash_amount);
    const prepareData = {
      create_date: getTodayJalaali(),
      card_amount,
      type: this.state.type
    };
    console.log("sent card amount", prepareData.card_amount);
    this.props
      .addPaymentToBill(this.props.pk, prepareData)
      .then(() => {
        toastr.success("    ");
        this.props.refetch();
      })
      .catch(() => {
        toastr.error("   ");
      });
    this.props.onClose();
  };

  render() {
    return (
      <Modal
        dimmer="blurring"
        open={this.props.open}
        onClose={this.props.onClose}
        className="rtl text-right"
        size="tiny"
      >
        <Modal.Header className="d-flex">
          <h3
            className="yekan d-flex"
            style={{ alignItems: "center", marginBottom: 0 }}
          >
              
          </h3>
        </Modal.Header>
        <Modal.Content>
          <h5 className="yekan">
              &nbsp;
            <span
              style={{ fontWeight: "bold", color: "red" }}
              onChange={this.handleTypeChange}
            >
              *
            </span>
          </h5>
          <Dropdown
            placeholder="  "
            className="yekan"
            fluid
            selection
            options={this.state.paymentOptions}
            onChange={this.setStateType}
          />
          {this.state.type === "cheque" && (
            <React.Fragment>
              <h5 className="yekan">
                 &nbsp;
                <span style={{ fontWeight: "bold", color: "red" }}>*</span>
              </h5>
              <Input
                type="number"
                fluid
                className="ltr"
                defaultValue={this.props.price}
              />
              <h5 className="yekan">
                 &nbsp;
                <span style={{ fontWeight: "bold", color: "red" }}>*</span>
              </h5>
              <Input
                type="number"
                fluid
                className="rtl placeholder-rtl"
                placeholder=" "
              />
              <h5 className="yekan">
                &nbsp;
                <span style={{ fontWeight: "bold", color: "red" }}>*</span>
              </h5>
              <Input
                fluid
                className="rtl placeholder-rtl text-right yekan"
                placeholder=""
              />
              <h5 className="yekan">
                 &nbsp;
                <span style={{ fontWeight: "bold", color: "red" }}>*</span>
              </h5>
              <Input
                fluid
                className="rtl placeholder-rtl text-right yekan"
                placeholder=": 1398/1/10"
              />
              <h5 className="yekan">
                 &nbsp;
                <span style={{ fontWeight: "bold", color: "red" }}>*</span>
              </h5>
              <Input
                fluid
                className="rtl placeholder-rtl text-right yekan"
                placeholder=": 1398/1/10"
              />
            </React.Fragment>
          )}
          {this.state.type === "cash_card" && (
            <React.Fragment>
              <h5 className="yekan">
                  &nbsp;
                <span style={{ fontWeight: "bold", color: "red" }}>*</span>
              </h5>
              <Input
                fluid
                className="ltr"
                type="number"
                defaultValue={this.props.price}
                onChange={e => {
                  this.handleInputChange(e, "card_amount");
                }}
              />
              <h5 className="yekan">  </h5>
              <Input
                fluid
                className="ltr"
                type="number"
                defaultValue="0"
                onChange={e => {
                  this.handleInputChange(e, "cash_amount");
                }}
              />
            </React.Fragment>
          )}
        </Modal.Content>
        <Modal.Actions className="ltr text-center">
          <Button.Group>
            <Button className="yekan" onClick={this.props.onClose}>
              
            </Button>
            <Button.Or text="" className="yekan" />
            <Button className="yekan" positive onClick={this.handleSubmit}>
              
            </Button>
          </Button.Group>
        </Modal.Actions>
      </Modal>
    );
  }
}

export default connect(null, { addPaymentToBill, getTodayJalaali })(
  AddPaymentModal
);
